import React, { useEffect, useState } from "react";
import { graphql, useStaticQuery } from "gatsby";

import { ILang } from "../../types/common.type";
import { IContactFromQueryResult } from "../../types/form.type";

import ContactForm from "./contactForm";
import ContentContainer from "../common/contentContainer";

const ContactFormSection = ({ lang }: ILang) => {
  const formMdData = useStaticQuery(query) as IContactFromQueryResult;

  // formDescription is set by useState + useEffect in order to avoid production errors that ssr doesn't match client render
  const [formDescription, setFormDescription] = useState<
    string | null | undefined
  >(null);
  useEffect(() => {
    if (lang in formMdData)
      setFormDescription(formMdData?.[lang]?.nodes?.[0]?.html as string | null);
    else setFormDescription(formMdData?.en?.nodes?.[0]?.html);
  }, [lang, formMdData]);

  const allMarkup = formMdData?.markupData?.nodes?.find(
    (node) => node.fields.language === lang
  )?.frontmatter;
  const formMarkup = allMarkup?.contactForm;
  const validationMarkup = allMarkup?.validation;

  return (
    <>
      {formMarkup ? (
        <>
          <section className={`relative w-full bg-secondary-100 py-16`}>
            <div
              id="contact-form"
              className="absolute -top-16 opacity-0 pointer-events-none"
            >
              anchor
            </div>
            <ContentContainer
              maxWidthClass="max-w-[1280px]"
              className={`flex-col items-center text-main-100`}
            >
              <div className="flex justify-between w-full items-center pb-8 sm:pb-14 flex-col sm:flex-row gap-5">
                <h2 className={`text-4xl font-bold w-full text-main-100`}>
                  {formMarkup?.sectionTitle.toUpperCase()}
                </h2>
                <div
                  className={`w-full sm:w-auto text-start sm:text-end text-xl font-normal whitespace-nowrap text-main-100`}
                >
                  {formMarkup?.sectionLinkTitle.toUpperCase()}
                </div>
              </div>
              <div className="flex flex-col md:flex-row gap-10 lg:gap-28 justify-center max-w-4.5xl items-stretch">
                {formDescription ? (
                  <p
                    className="contact-form-description flex-1 text-xl font-light w-full"
                    dangerouslySetInnerHTML={{ __html: formDescription }}
                  />
                ) : null}
                <ContactForm
                  markup={formMarkup}
                  validationMarkup={validationMarkup}
                />
              </div>
            </ContentContainer>
          </section>
        </>
      ) : null}
    </>
  );
};

export default ContactFormSection;

export const query = graphql`
  query {
    en: allMarkdownRemark(
      filter: {
        fields: { sourceName: { eq: "contactForm" }, language: { eq: "en" } }
      }
    ) {
      nodes {
        html
      }
    }
    ru: allMarkdownRemark(
      filter: {
        fields: { sourceName: { eq: "contactForm" }, language: { eq: "ru" } }
      }
    ) {
      nodes {
        html
      }
    }
    markupData: allMarkdownRemark(
      filter: { fields: { sourceName: { eq: "common" } } }
    ) {
      nodes {
        fields {
          language
        }
        frontmatter {
          validation {
            invalidEmail
            required
            consent
          }
          contactForm {
            sectionTitle
            sectionLinkTitle
            emailLabel
            nameLabel
            companyNameLabel
            servicesLabel
            servicesOptions {
              web_dev
              mobile_dev
              qa
              devops
              ui_ux
              ba
              add_ins
              vr_ar
              support
              consulting
            }
            budgetLabel
            budgetOptions {
              _1500_2500
              _2500_5000
              _5000_10000
              _10000_
              not_decided
            }
            selectPlaceholder
            descriptionLabel
            descriptionPlaceholder
            privacyPolicyCheckboxLabel
            adsCheckboxLabel
            buttonLabel
          }
        }
      }
    }
  }
`;
