import React, { useState } from "react";
import { Controller, SubmitHandler, useForm } from "react-hook-form";
import { yupResolver } from "@hookform/resolvers/yup";
import { AxiosResponse } from "axios";

import {
  IContactFormMarkup,
  TValidationMarkup,
} from "../../types/commonMarkup.type";
import {
  BudgetOptions,
  IContactFormData,
  ServicesOptions,
} from "../../types/form.type";

import { contactFormSchema } from "../../tools/formSchemas";
import { getCookie } from "../../tools/cookieGetter";
import { hubspotApi } from "../../tools/api";
import {
  getValidationMessage,
  mapContactFormData,
} from "../../tools/formsTools";

import SkeletonStub from "../common/skeletonStub";
import { ContactFormInput, ControlLabel } from "./inputs";
import { Checkbox, MenuItem } from "@mui/material";

const ContactForm = ({
  markup,
  validationMarkup,
}: {
  markup: IContactFormMarkup;
  validationMarkup?: TValidationMarkup;
}) => {
  const [isPending, setIsPending] = useState<boolean>(false);
  const [isGivenConsent, setIsGivenConsent] = useState<boolean>(true);

  const {
    register,
    handleSubmit,
    control,
    reset,
    formState: { errors, isValid },
  } = useForm<IContactFormData>({
    mode: "onBlur",
    resolver: yupResolver(contactFormSchema),
    defaultValues: {
      consentToProcess: true,
    },
  });

  const onSubmit: SubmitHandler<IContactFormData> = async (data) => {
    if (!isValid) return;

    setIsPending(true);

    const hutk = getCookie("hubspotutk");

    const requestBody = mapContactFormData(data, hutk);

    try {
      const response = await hubspotApi.post<
        any,
        AxiosResponse<{ inlineMessage: string }>,
        any
      >("/3eae609c-bcf2-47f4-ad93-11a65c4b4f9d", requestBody);

      if (response.status === 200) reset();
    } catch (e: any) {
      console.log("request err>>", e.response?.data?.message);
    }

    setIsPending(false);
  };
  const renderTextInput = (
    key: keyof IContactFormData,
    label: string,
    multiline?: boolean,
    rows?: number
  ) => {
    return (
      // <ContactFormInput
      //   id={key}
      //   label={label}
      //   variant="outlined"
      //   error={!!errors[key]?.message}
      //   helperText={getValidationMessage(key, errors, validationMarkup)}
      //   aria-label={key}
      //   fullWidth
      //   multiline={multiline}
      //   rows={rows}
      //   size="small"
      //   {...register(key)}
      // />
      <input id={key} aria-label={key} {...register(key)} />
    );
  };

  return (
    <form
      onSubmit={handleSubmit(onSubmit)}
      className="flex-1 flex flex-col gap-2.5"
    >
      {renderTextInput("email", markup.emailLabel)}
      {renderTextInput("company", markup.companyNameLabel)}
      {renderTextInput("firstname", markup.nameLabel)}

      {/* <ContactFormInput
        id={"services"}
        label={markup.servicesLabel}
        variant="outlined"
        select
        defaultValue=""
        error={!!errors["services"]?.message}
        helperText={getValidationMessage("services", errors, validationMarkup)}
        aria-label={"services"}
        fullWidth
        size="small"
        {...register("services")}
        >
        {Object.values(ServicesOptions).map((service) => (
          <MenuItem key={service} value={service}>
            {markup.servicesOptions[service]}
          </MenuItem>
        ))}
      </ContactFormInput> */}
      {renderTextInput("services", markup.nameLabel)}

      {/* <ContactFormInput
        id={"budget"}
        label={markup.budgetLabel}
        variant="outlined"
        select
        defaultValue=""
        error={!!errors["budget"]?.message}
        helperText={getValidationMessage("budget", errors, validationMarkup)}
        aria-label={"budget"}
        fullWidth
        size="small"
        {...register("budget")}
      >
        {Object.values(BudgetOptions).map((budget) => (
          <MenuItem key={budget} value={budget}>
            {markup.budgetOptions[budget]}
          </MenuItem>
        ))}
      </ContactFormInput> */}
      {renderTextInput("budget", markup.nameLabel)}

      {renderTextInput(
        "describe_your_project",
        markup.descriptionLabel,
        true,
        5
      )}

      <Controller
        control={control}
        name="consentToProcess"
        render={({ field: { onChange, ref, value } }) => (
          <ControlLabel
            control={
              <Checkbox
                id={"consentToProcess"}
                checked={value}
                onChange={(e) => {
                  onChange(e);
                  setIsGivenConsent(!!e.target.checked);
                }}
                sx={{
                  color: !isGivenConsent ? "#D32F2F" : "#111",
                  "&.Mui-checked": {
                    color: "#111",
                  },
                }}
                inputRef={ref}
              />
            }
            label={markup.privacyPolicyCheckboxLabel}
            aria-label={"consentToProcess"}
            sx={{ marginTop: "10px" }}
          />
        )}
      />
      {errors.consentToProcess?.message || !isGivenConsent ? (
        <p className="text-[10.5px] text-[#D32F2F] ml-3">
          {validationMarkup?.consent}
        </p>
      ) : null}

      <ControlLabel
        control={
          <Checkbox
            defaultChecked
            sx={{
              color: "#111",
              "&.Mui-checked": {
                color: "#111",
              },
            }}
          />
        }
        label={markup.adsCheckboxLabel}
        aria-label={"adsCheckboxLabel"}
      />

      <div className="flex items-center justify-center pt-5 text-main-100">
        <button
          type="submit"
          aria-label={markup.buttonLabel}
          disabled={isPending || !isGivenConsent}
          className={`px-0 sm:px-5 md:px-6 font-bold whitespace-nowrap rounded-lg mx-0  text-secondary-100 h-12 flex items-center justify-center disabled:bg-main-70 hover:bg-main-70 px-6 relative ${
            isPending ? "bg-main-70" : "bg-main-100"
          }`}
        >
          {isPending ? <SkeletonStub /> : null}
          <span className="z-20">{markup.buttonLabel.toUpperCase()}</span>
        </button>
      </div>
    </form>
  );
};

export default ContactForm;
